<!-- 组件说明 -->
<template>
  <swiper :options='swiperOption' ref="mySwiper">
    <div class="mine-scroll-pull-down" v-if="pullDown">
      <me-loading :text="pullDownText" inline ref="pullDownLoading"></me-loading>
    </div>
    <swiper-slide>
      <slot></slot>
    </swiper-slide>
    <div class="mine-scroll-pull-up" v-if="pullUp">
      <me-loading :text="pullUpText" inline ref="pullUpLoading"></me-loading>
    </div>
    <div class="swiper-scrollbar" v-if="scrollbar" slot="scrollbar"></div>
  </swiper>
</template>

<script>
  import {
    swiper,
    swiperSlide
  } from 'vue-awesome-swiper';
  import MeLoading from 'base/loading';
  import {
    PULL_DOWN_HEIGHT,
    PULL_DOWN_TEXT_INIT,
    PULL_DOWN_TEXT_START,
    PULL_DOWN_TEXT_ING,
    PULL_DOWN_TEXT_END,
    PULL_UP_HEIGHT,
    PULL_UP_TEXT_INIT,
    PULL_UP_TEXT_START,
    PULL_UP_TEXT_ING,
    PULL_UP_TEXT_END
  } from './config';

  export default {
    name: 'MeScroll',
    components: {
      swiperSlide,
      swiper,
      MeLoading
    },
    props: {
      scrollbar: {
        type: Boolean,
        default: true
      },
      data: {
        type: [Array, Object]
      },
      pullDown: {
        type: Boolean,
        default: false
      },
      pullUp: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {

      };
    },
    watch: {
      data() {
        this.update();
      }
    },
    created() {
      this.init();
    },
    mounted() {

    },
    methods: {
      update() {
        this.$refs.mySwiper && this.$refs.mySwiper.swiper.update();
      },
      scrollToTop(speed, runCallbacks) {
        this.$refs.mySwiper && this.$refs.mySwiper.swiper.slideTo(0, speed, runCallbacks);
      },
      init() {
        this.pulling = false;
        this.pullDownText = PULL_DOWN_TEXT_INIT;
        this.pullUpText = PULL_UP_TEXT_INIT;
        this.swiperOption = {
          direction: 'vertical',
          slidesPerView: 'auto',
          freeMode: true,
          setWrapperSize: true,
          scrollbar: {
            el: this.scrollbar ? '.swiper-scrollbar' : null,
            hide: true
          },
          on: {
            sliderMove: this.scroll,
            touchEnd: this.touchEnd,
            transitionEnd: this.scrollEnd
          }
        };
      },
      scroll() {
        const swiper = this.$refs.mySwiper.swiper;
        this.$emit("scroll", swiper.translate, swiper);

        if (this.pulling) {
          return;
        }

        if (swiper.translate > 0 && this.pullDown) {
          if (swiper.translate > PULL_DOWN_HEIGHT) {
            this.$refs.pullDownLoading.setLoadingText(PULL_DOWN_TEXT_START);
          } else {
            this.$refs.pullDownLoading.setLoadingText(PULL_DOWN_TEXT_INIT);
          }
        }

        if (swiper.isEnd && this.pullUp) {
          const isPullUp = Math.abs(swiper.translate) + swiper.height - parseInt(swiper.$wrapperEl.css('height')) >
            PULL_UP_HEIGHT;

          if (isPullUp) {
            this.$refs.pullUpLoading.setLoadingText(PULL_UP_TEXT_START);
          } else {
            this.$refs.pullUpLoading.setLoadingText(PULL_UP_TEXT_INIT);
          }
        }

      },

      scrollEnd() {
        const swiper = this.$refs.mySwiper.swiper;
        this.$emit('scroll-end', swiper.translate, swiper, this.pulling);
      },

      touchEnd() {
        if (this.pulling) {
          return;
        }
        const swiper = this.$refs.mySwiper.swiper;
        if (swiper.translate > PULL_DOWN_HEIGHT && this.pullDown) {
          console.log("pulldown load");

          this.pulling = true;
          swiper.allowTouchMove = false;
          swiper.setTransition(swiper.params.speed);
          swiper.setTranslate(PULL_DOWN_HEIGHT);
          swiper.params.virtualTranslate = true;
          this.$refs.pullDownLoading.setLoadingText(PULL_DOWN_TEXT_ING);
          this.$emit('pull-down', this.pullDownEnd);
        }

        if (swiper.isEnd && this.pullUp) {
          const isPullUp = Math.abs(swiper.translate) + swiper.height - parseInt(swiper.$wrapperEl.css('height')) >
            PULL_UP_HEIGHT;
          if (!isPullUp) {
            return;
          }
          console.log("pullup load");
          this.pulling = true;
          swiper.allowTouchMove = false;
          swiper.setTransition(swiper.params.speed);
          swiper.setTranslate(swiper.height - parseInt(swiper.$wrapperEl.css('height')) - PULL_UP_HEIGHT);
          swiper.params.virtualTranslate = true;
          this.$refs.pullUpLoading.setLoadingText(PULL_UP_TEXT_ING);
          this.$emit('pull-up', this.pullUpEnd);
        }
      },

      pullDownEnd() {
        this.pulling = false;
        const swiper = this.$refs.mySwiper.swiper;
        this.$refs.pullDownLoading.setLoadingText(PULL_DOWN_TEXT_END);
        swiper.params.virtualTranslate = false;
        swiper.allowTouchMove = true;
        swiper.setTransition(swiper.params.speed);
        swiper.setTranslate(0);
        setTimeout(() => {
          this.$emit('pull-down-transition-end');
        }, swiper.params.speed);
      },

      pullUpEnd() {
        this.pulling = false;
        const swiper = this.$refs.mySwiper.swiper;
        this.$refs.pullUpLoading.setLoadingText(PULL_UP_TEXT_END);
        swiper.params.virtualTranslate = false;
        swiper.allowTouchMove = true;
        // swiper.setTransition(swiper.params.speed);
        // swiper.setTranslate(swiper.height - parseInt(swiper.$wrapperEl.css('height')));
      }
    }
  }

</script>

<style lang='scss' scoped>
  .swiper-container {
    overflow: hidaden;
    width: 100%;
    height: 100%;
  }

  .swiper-slide {
    height: auto;
  }

  .mine-scroll-pull-down,
  .mine-scroll-pull-up {
    position: absolute;
    left: 0;
    width: 100%;
  }

  .mine-scroll-pull-down {
    bottom: 100%;
    height: 80px;
  }

  .mine-scroll-pull-up {
    top: 100%;
    height: 30px;
  }

</style>



