<!-- 组件说明 -->
<template>
  <swiper :options="swiperOption" :key="keyId">
    <slot></slot>
    <div class="swiper-pagination" v-if="pagination" slot="pagination"></div>
  </swiper>
</template>

<script>
  import {
    swiper
  } from 'vue-awesome-swiper';

  export default {
    name: "MeSlider",
    components: {
      swiper
    },
    props: {
      direction: {
        type: String,
        default: 'horizontal',
        validator(value) {
          return ['horizontal', 'vertical'].indexOf(value) > -1;
        }
      },
      interval: {
        type: Number,
        default: 3000,
        validator(value) {
          return value >= 0;
        }
      },
      loop: {
        type: Boolean,
        default: true
      },
      pagination: {
        type: Boolean,
        default: true
      },
      dataList: {
        type: Array,
        default () {
          return [];
        }
      }
    },
    data() {
      return {
        keyId: Math.random()
      }
    },
    computed: {

    },
    watch: {
      dataList(newData) {
        if (newData.length === 0) {
          return;
        }
        this.swiperOption.loop = newData.length <= 1 ? false : this.loop;
        this.keyId = Math.random();
      }
    },
    created() {
      this.init();
    },
    methods: {
      init() {
        this.swiperOption = {
          watchOverflow: true,
          direction: this.direction,
          autoplay: this.interval ? {
            delay: this.interval,
            disableOnInteraction: false
          } : false,
          slidesPerView: 1,
          loop: this.dataList.length <= 1 ? false : this.loop,
          pagination: {
            el: this.pagination ? '.swiper-pagination' : null
          }
        }
      }
    }
  }

</script>

<style lang='scss' scoped>
  //@import url()
  .swiper-container {
    width: 100%;
    height: 100%;
  }

</style>



